@import '../style-variables.css';

.dashboard-page {
  display: flex;
  /* justify-content: space-between; */
  min-height: calc(100vh - var(--nav-height));

  --acc-nav-width: 18%;
}

.dashboard-page .account-pane {
  padding: 15px;
  width: calc(100% - var(--acc-nav-width));
  background-color: var(--darkgrey);
}

/* ===================================================== */
/* Nav Sidebar */

.account-nav {
  background: var(--offwhite);
  padding: 15px;
  width: var(--acc-nav-width);
  position: sticky;
  top: var(--nav-height);
  height: calc(100vh - var(--nav-height));
  line-height: 1.5;
  color: var(--darkgrey);
}

.account-nav .user-name,
.account-nav .nav-link {
  padding: 5px 10px;
}

.account-nav .user-name {
  font-weight: bold;
  text-decoration: underline;
}

.account-nav .nav-link {
  display: block;
  width: 100%;
  text-align: center;
  border-radius: 10px;
  text-decoration: none;
  color: var(--midgrey);
  margin: 10px 0px;
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}

.account-nav .nav-link.active {
  background-color: var(--purple);
  color: var(--offwhite);
}

.account-nav ul {
  margin: 0;
  list-style-type: none;
}
.account-nav ul li {
  padding: 25px 0;
}

.account-nav ul li a {
  text-decoration: none;
  font-size: 18px;
  color: var(--darkgrey);
}

.account-title {
  font-size: 35px;
  color: var(--offwhite);
  text-align: left;
}

.account-sub-title {
  font-size: 20px;
  color: var(--offwhite);
  text-align: left;
  /* padding: 15px 0; */
  line-height: 3;
}

/* ===================================================== */
