.watchlist-wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.watchlist-movie {
  display: flex;
  flex-direction: column;
  padding: 15px;
  width: 20%;
}

.watchlist-filter {
  display: flex;
  justify-content: flex-end;
}

.watchlist-filter-btn {
  background: none;
  border: none;
  margin: none;
}

.watchlist-buttons {
  display: flex;
  padding: 5px 0;
  justify-content: space-between;
}

.watchlist-btn {
  font-size: 14px;
  margin: 0;
}
