/* Variables */
:root {
  --purple: #88388c;
  --red: #db2b39;
  --lightgrey: #747180;
  --midgrey: #5a5a5a;
  --darkgrey: #23262e;
  --offwhite: #f5f8e8;
  --popcornyellow: #ffe695;

  --nav-height: 65px;
  --fontfamily: Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue',
    sans-serif;
}

/* purple: rgb(136, 56, 140)
red: rgb(219, 43, 57)
lightgrey: rgb(116, 113, 128)
midgrey: rgb(90, 90, 90)
darkgrey: rgb(35, 38, 46)
offwhite: rgb(245, 248, 232)
popcornyellow: rgb(255, 230, 149)
*/

.movie-card {
  --modalheight: 82vh;
  --modalwidth: 88vw;

  display: -webkit-flex;

  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 35px 0px;
  z-index: 10;
  position: fixed;
  top: var(--nav-height);
  width: 100%;
  height: 100%;
  font-family: var(--fontfamily);
  color: var(--midgrey);
  background-color: rgba(35, 38, 46, 0.6);
}

/* ======================================================= */
/* Backdrop */

.movie-card .backdrop {
  border-radius: 20px;
  height: var(--modalheight);
  width: var(--modalwidth);
  position: absolute;
  z-index: -1;
  overflow: hidden;
  background-color: var(--purple);
  background-position-x: 15vw;
  background-size: cover;
}

.movie-card .backdrop img {
  width: 100%;
  z-index: -2;
  border-radius: 20px;
  position: absolute;
}

.movie-card .backdrop .overlay {
  background-color: var(--darkgrey);
  opacity: 0.5;
  height: 100%;
  width: 100%;
  position: absolute;
}

/* ======================================================= */
/* ======================================================= */
/* Info section */

.movie-card .info {
  border-radius: 20px;
  background: linear-gradient(to right, var(--offwhite) 50%, transparent 100%);
  position: absolute;
  height: var(--modalheight);
  width: var(--modalwidth);
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

/* iPhoneX screen width */
@media only screen and (max-width: 375px) {
  .movie-card .info {
  }
}

