/* Info Heading */

.movie-card .info .heading {
  position: relative;
  padding: 25px;
  height: 45%;
  overflow: hidden;
}

.movie-card .heading .poster {
  position: relative;
  float: left;
  margin-right: 20px;
  height: 100%;
  box-shadow: 0 0 20px -10px rgba(0, 0, 0, 0.5);
}

@media only screen and (max-width: 600px) {
