    display: none;
  }
}

.movie-card .heading h1 {
  color: var(--darkgrey);
  font-weight: 500;
  margin: 0;
  font-size: calc(12px + 2vw);
}

.movie-card .heading h4 {
  font-weight: 400;
  margin: 0;
  font-size: 1.8vw;
}

