/* ======================================================= */
/* Close Button */

.movie-card .close-modal {
  position: absolute;
  cursor: pointer;
  right: 0px;
  z-index: 1;
  border-radius: 20px;
  border: solid;
  border-color: var(--offwhite);
  border-width: 1px;
  width: 40px;
  height: 40px;
