.nav-item {
  position: relative;
  color: var(--offwhite);
  text-decoration: none;
  margin-left: 20px;
  font-family: var(--fontfamily);
  padding: 10px;
  font-size: 18px;
}

.nav-item:before,
.nav-item:after {
  position: absolute;
  width: 1px;
  height: 100%;
  /* transform: translateY(100%); */
  background: #fff;
  content: '';
  opacity: 0.2;
  transition: all 0.3s;
  pointer-events: none;
}

.nav-item:before {
  transition: .3s ease;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  background: var(--offwhite);
  content: "";
  opacity: .1;
}

.nav-item:after {
  transition: .3s ease;
  position: absolute;
  bottom: 0;
  top: 0;
  right: 0;
  background: var(--offwhite);
  content: "";
  opacity: .1;
}

.nav-item:hover::before {
  left: 50%;
  opacity: 1;
  height: 100%;
  -webkit-transform: rotate(90deg) translateY(50%);
          transform: rotate(90deg) translateY(50%);
  -webkit-transform-origin: 0 100%;
          transform-origin: 0 100%;
}
.nav-item:hover::after {
  /* right: 50%; */
  height: 100%;
  opacity: 1;
  right: 50%;
  -webkit-transform: rotate(-90deg) translateY(-50%);
          transform: rotate(-90deg) translateY(-50%);
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
}

